.animation {
    -webkit-animation: translation 3s;
    -webkit-animation-iteration-count: infinite;
    -moz-animation: translation 3s;
    -moz-animation-iteration-count: infinite;
    -ms-animation: translation 3s;
    -ms-animation-iteration-count: infinite;
    -o-animation: translation 3s;
    -o-animation-iteration-count: infinite;
    animation: translation 3s;
    animation-iteration-count: infinite;
    white-space: nowrap;
}

.text{
    text-align: center;
    margin: 4px;
}


@-webkit-keyframes translation {
    0%    { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
    25%   { -webkit-transform: translate(0px,0px); transform: translate(0px,0px);    }
    50%   { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
    75%   { -webkit-transform: translate(0px,0px); transform: translate(0px,0px);    }
    100%  { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
}
@keyframes translation {
    0%    { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
    25%   { -webkit-transform: translate(0px,0px); transform: translate(0px,0px);    }
    50%   { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
    75%   { -webkit-transform: translate(0px,0px); transform: translate(0px,0px);    }
    100%  { -webkit-transform: translate(0px,150px); transform: translate(0px,150px);  }
}



